.card-title{
  color: black;
  background-color: white;
}
.vedio{
  position: absolute;
  height: 100%;
  width: 100%;
  right: 0;
  bottom: 0;
  padding: 0.01rem;
  opacity: 0.6;
}

.dropdown-item {
  display: block; 
  width: auto; 
  padding: var(--bs-dropdown-item-padding-y) var(--bs-dropdown-item-padding-x);
  clear: both;
  font-weight: 400;
  color: var(--bs-dropdown-link-color);
  text-align: inherit; 
  text-decoration: none; 
  white-space: break-spaces; 
  background-color: transparent;
  border: 0;
}
@media screen and (min-width:512px) and (max-width: 768px){
   .cfcont{
     width: 31rem;
   }
   .cardstyle{
      width: 8rem;
      height: 8rem;
    }
}
@media screen and (max-width: 352px){
  .cfcont{
    width: 12.7rem;
  }
  .cardstyle{
    width: 10rem;
    height: 10rem;
  }
}
@media screen and (min-width:353px) and (max-width: 511px){
  .cfcont{
    width: 21rem;
  }
  .cardstyle{
    width: 8rem;
    height: 8rem;
  }
}
@media screen and (min-width:769px){
  .cfcont{
    width: 37rem;
  }
  .cardstyle{
    width: 10rem;
    height: 10rem;
  }
}